.header {
    grid-area: h;
    background-color: #26a69a;
    display: grid;
    grid-template-areas:
    "txlog acc srch";
    grid-template-columns: 1fr 9fr 1fr;
    grid-template-rows: 1fr;
}

.textLogo {
    grid-area: txlog;
    background-color: #26a69a;
    font-size: 20px;
    padding-left: 15px;
    line-height: 50px;
}
.account {
    grid-area: acc;
    background-color: #2e2e2e;
}

.search {
    grid-area: srch;
}
.login{
    padding-top: 10px;
    padding-left: 10px;
    color:#26a69a;
}